import './App.css';
import {Component} from 'react'
import {HashRouter as Router, Route} from 'react-router-dom'
import Redirect from "./components/redirect";
import Home from "./components/home"
import RedirectMag from './components/magnet';
class App extends Component{
  componentDidMount(){
    console.log('wo');
  }
  render(){
    return(
        <Router>
        <Route path='/url/:url' exact component={Redirect}/>
        <Route path='/' exact component={Home}/>
        <Route path='/mag/:magnet' exact component={RedirectMag}/>
        </Router>
    )
  }
}
export default App;
