import { Component } from "react";
import './home.css'
class Home extends Component{
    state = {
        location:''
    }
    componentDidMount(){
        this.setState({location:document.location.origin})
    }
    render(){
        return(
            <div className='App'>
                <div className='container'>
                <div className='url'>
                {this.state.location}/url/{`<site-name>`} for redirecting to a webpage
                </div>
                <br/>
                <div className='mag'>
                {this.state.location}/mag/{`<magnet-link>`} for redirecting to a magnet-link
                </div>
                
                <span><b>Note:</b> Do not add https:// or magnet:// in the url</span>
                </div>
            </div>
        )
    }
}

export default Home;